<template lang="html">
<div class="privado">
  <div class="row">
  <nav class="navbar navbar-expand-lg navbar-light bg-primary col-12">
    <a class="navbar-brand" href="#">{{usuario.email}}</a>
    <button type="submit" class="btn btn-success my-2 my-sm-0 offset-8" v-on:click="signOut">Sign Out</button>
</nav>
</div><br>
<div class="container">
      <div class="card"  id="cardd">
        <div class="card-body">
          <h5 class="card-title">Agregar</h5>
          <input type="text" placeholder="Titulo" v-model="nuevaNota.texto2"><br><br>
          <input type="file"><br><br>
          <button type="submit" class="btn btn-primary" v-on:click="add">ADD</button>
        </div>
      </div>
</div><br>
<div class="card" id="tabla">
  <div class="card-header" id="headert">
    Datos
  </div>
  <table class="card-table table">
    <thead>
      <tr>
        <th scope="col">Autor</th>
        <th scope="col">Titulo</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="nota in notas" :key="nota.i">
        <td>{{nota.texto1}}</td>
        <td>{{nota.texto2}}</td>
      </tr>
      
    </tbody>
  </table>
</div>
</div>
</template>

<script lang="js">
import {db} from '../db.js'
import firebase from "firebase"
  export default  {
    name: 'privado',
    props: [],
    mounted () {

    },
    data () {
      return {
        notas:[],
        usuario:firebase.auth().currentUser,
        nuevaNota:{
          texto1:"",
          texto2:""
        }
      }
    },
    methods: {
      add:function(){
        this.nuevaNota.texto1=this.usuario.email;
        db.collection("documents").add(this.nuevaNota);
        this.nuevaNota.texto1="";
        this.nuevaNota.texto2="";
      },
      signOut:function(){
        firebase.auth().signOut().then(()=> {
          this.$router.replace('/login');
        }).catch(function(error) {
          alert(error)
});
      }
    },
    computed: {

    },
    firestore: {
      notas:db.collection("documents"),
    }
}


</script>

