<template lang="html">

  <section class="login">
    <h1>login Firebase</h1>
      <input type="text" id="login" class="fadeIn second" name="login" placeholder="login" v-model="email">
      <input type="password" id="password" class="fadeIn third" name="login" placeholder="password" v-model="cont" v-on:keyup.enter="aut"><br><br>
      <button class="btn bg-primary"  v-on:click="aut">Login</button><br><br>
      <button class="btn bg-primary"  v-on:click="autfac">Login with Facebook</button>
      <br><router-link to="/registro">Registrarse</router-link>

  </section>

</template>

<script lang="js">
import firebase from 'firebase'
  export default  {
    name: 'login',
    props: [],
    mounted () {

    },
    data () {
      return {
        email:"",
        cont:""        
      }
    },
    methods: {
      aut() {
        firebase.auth().signInWithEmailAndPassword(this.email, this.cont).then(()=>{
          this.$router.replace('/privado');
        })
        .catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        alert(errorCode,errorMessage);
      });

      },
      autfac() {
        var provider = new firebase.auth.FacebookAuthProvider();
        firebase.auth().useDeviceLanguage();
        provider.setCustomParameters({'display':'popup'});
        firebase.auth().signInWithPopup(provider).then(()=> {
          this.$router.replace('/privado');
      }).catch((error)=> {
        alert(error)
      });
      }
    },
    computed: {

    }
}


</script>

<style scoped>
  .login {

  }
  input{
    border-radius:1rem;
    width:30%;
  }
  section{
    background-color: coral;
    width:80%;
    margin-left:10%;
    margin-right:10%;
    border-radius:1rem;
    box-shadow: 1rem 1rem 1rem darkturquoise;
  }
</style>
