<template lang="html">

  <section class="registro">
    <h1>registro Firebase</h1>
      <input type="text" id="login" class="fadeIn second" name="login" placeholder="login" v-model="email">
      <input type="password" id="password" class="fadeIn third" name="login" placeholder="password" v-model="cont"><br><br>
      <button class="btn bg-primary"  v-on:click="login">Registrar</button><br><br>
      <router-link to="/login">Login</router-link>
  </section>

</template>

<script lang="js">
import firebase from 'firebase'
  export default  {
    name: 'registro',
    props: [],
    mounted () {

    },
    data () {
      return {
        email:"",
        cont:""
      }
    },
    methods: {
      login() {
        firebase.auth().createUserWithEmailAndPassword(this.email, this.cont).then(()=>{
          this.$router.replace('/privado');
        })
        .catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        alert(errorCode,errorMessage);
      });

      },
    },
    computed: {

    }
}


</script>

